<script>
  import { onMount } from "svelte";
  import Stat from "./Stat.svelte";
  let name;
  let stats;
  let lastUpdated = new Date();

  async function fetchData() {
    console.log("updated");
    await fetch(`https://quiz-nitcieee.herokuapp.com/api/stats`)
      .then(r => r.json())
      .then(data => {
        stats = data;
        lastUpdated = new Date();
      });
  }
  onMount(fetchData);
</script>

<style>
  main {
    display: grid;
    width: 100%;
    gap: 1em;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
      "header header"
      "stat1 stat2"
      "stat3 stat4"
      "date date";
  }
  header {
    grid-area: header;
  }
  .date {
    grid-area: date;
    color: #8a93b9;
  }
</style>

<main>
  <header
    style="margin-top: 0.5rem; margin-bottom: 0.5rem; align-items: center;">
    <img
      src="https://quiz.nitcieee.tech/email-nitc.png"
      style="height: 50px; margin-right: 0.5rem;"
      alt="NITC logo" />
    <img
      src="https://quiz.nitcieee.tech/email-ieee.png"
      style="height: 50px; margin-right: 0.5rem;"
      alt="IEEE logo" />
    <img
      src="https://quiz.nitcieee.tech/email-ias-logo-lg.png"
      style="height: 60px;"
      alt="IAS logo" />
  </header>
  <Stat
    type="Total Participants"
    value={stats ? stats.totalParticipants : 'Loading'} />
  <Stat
    type="Total Submissions"
    value={stats ? stats.totalSubmissions : 'Loading'} />
  <Stat type="IEEE Members" value={stats ? stats.ieeeMembers : 'Loading'} />
  <Stat type="Highest Score" value={stats ? stats.highestScore : 'Loading'} />
  <div class="date">Last updated: {lastUpdated.toLocaleString()}</div>
</main>
